	computed:{
		getheight(){
			var gd;
			uni.getSystemInfo({
				success: (res) => {
					gd=res.windowHeight+'px';
				}
			})
			return gd;
		}
	},
	onShow() {
		console.log(this.getheight)
		uni.request({
			url:"https://unidemo.dcloud.net.cn/api/news",
			method:"GET",
			success: (res) => {
				console.log(res)
			}
		})
	}
}
